<template>

        <v-card class="text-left">
            <v-card-title>
                <div class="subtitle-1 font-weight-light text-left ml-3 mt-2"> Modification de l'agence </div>
                <v-spacer />
                <v-icon aria-label="Close" @click="dialog = false" >
                    mdi-close
                </v-icon>
            </v-card-title>

            <v-card-text>
                <v-row>
                    <v-col cols="12" md="6" >
                        <v-text-field class="purple-input" v-model="form.code" label="code"/>
                    </v-col>
                    <v-col cols="12" md="6" >
                        <v-text-field class="purple-input" v-model="form.nom" label="Nom"/>
                    </v-col>
                    <v-col cols="12" md="6" >
                        <v-text-field class="purple-input" v-model="form.ville" label="Ville"/>
                    </v-col>
                    <v-col cols="12" md="6" >
                        <v-text-field class="purple-input" v-model="form.quartier" label="Quartier"/>
                    </v-col>
                </v-row>
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-row>
                    <v-col cols="12" class="text-right" >
                        <el-button type="warning" round>Annuler</el-button>
                        <el-button type="primary" round>Continuer<i class="el-icon-arrow-right el-icon-right"></i></el-button>
                    </v-col>
                </v-row>
            </v-card-actions>
        </v-card>
    
</template>
<script>

    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';
    import moment from 'moment';

    export default {
        components: {
        },
        props: ['dialog'],
        data() {
            return {
                all_data: [],
                search: '',
                loading: true,
                form: '',
            }
        },

        created(){
            this.loadData();
        },
        methods: {

        async loadData()
        {
            NProgress.start();

            try {
              
                const response = await axios.get('v1/xperf/setting/agence/get');

                this.all_data = response.data;
                this.loading = false;


            } 
            catch (error) {

                this.loading = false;
                this.$notify({
                    type: 'error',
                    title: "Echec de recuperation"
                });
            }

            NProgress.done();

        },

            handleEdit(index, row) {
                this.dialog = true;
                this.form = row;
            },

            handleDelete(index, row) {
                console.log(index, row);
            }
        },
    }
</script>

<style>
    
    element.style {
        overflow:hiden;
    }
</style>